import { useSelector } from 'react-redux';

import {
  selectSentFrom,
  selectSentTo,
  selectStatus,
} from '../../redux/delivery/selectors';

import { StatusText, StatusTitle, StatusWrap } from './DeliveryStatus.styled';

const DeliveryStatus = () => {
  const sentFrom = useSelector(selectSentFrom);
  const sentTo = useSelector(selectSentTo);
  const status = useSelector(selectStatus);

  return (
    <StatusWrap>
      <StatusTitle>
         :{' '}
        <StatusText>
          {status.includes('  ') ? '  ' : status}
        </StatusText>
      </StatusTitle>
      <StatusTitle>
         : <StatusText>{sentFrom}</StatusText>
      </StatusTitle>
      <StatusTitle>
         : <StatusText>{sentTo}</StatusText>
      </StatusTitle>
    </StatusWrap>
  );
};
export default DeliveryStatus;
